import React from 'react';
import styles from './QuickOrderReduxForm.module.css';
import { reduxForm, Field } from 'redux-form';
import { required, maxLengthCreator, phoneNumber } from '../../../utils/validators/validators';
import { Input } from '../../../common/FormsControls/FormsControls';
import { Button } from 'react-bootstrap';

const maxLength100 = maxLengthCreator(100);
const maxLength50 = maxLengthCreator(50);
const maxLength20 = maxLengthCreator(20);

const QuickOrderForm = (props) => {
  return (
    <form onSubmit={props.handleSubmit}>
      <div className={styles.fieldContainer}>
        <div className={styles.titleField}>. :</div>
        <Field
          className={styles.field}
          name="email"
          component={Input}
          type="email"
          validate={[required, maxLength50,]}
          placeholder=". " />
      </div>
      <div className={styles.fieldContainer}>
        <div className={styles.titleField}>  :</div>
        <Field
          className={styles.field}
          name="name"
          component={Input}
          type="text"
          validate={[required, maxLength100,]}
          placeholder="  " />
      </div>
      <div className={styles.fieldContainer}>
        <div className={styles.titleField}> :</div>
        <Field
          className={styles.field}
          name="phone"
          component={Input}
          type="text"
          validate={[required, maxLength20, phoneNumber]}
          placeholder=" " />
      </div>
      <div className={styles.fieldContainer}>
        <div className={styles.titleField}>:</div>
        <Field
          className={styles.field}
          name="city"
          component={Input}
          type="text"
          validate={[required, maxLength50,]}
          placeholder='' />
      </div>
      <div className={styles.fieldContainer}>
        <div className={styles.titleField}>:</div>
        <Field
          className={styles.field}
          name="address"
          component={Input}
          type="text"
          validate={[required, maxLength50,]}
          placeholder='' />
      </div>
      <div>
      <Button variant='success' type='submit'> </Button>
      </div>
    </form>
  );
}

export const QuickOrderReduxForm = reduxForm({ form: 'order' })(QuickOrderForm)